"use client"

import { useState } from "react"
import { Settings, Scissors, Wrench, Sparkles, Ruler, CheckCircle } from "lucide-react"
import Image from "next/image"
import QuoteModal from "./quote-modal"

export default function CustomizedPartsCapabilitiesSection() {
  const [isQuoteModalOpen, setIsQuoteModalOpen] = useState(false)
  
  const capabilities = [
    {
      icon: Settings,
      title: "CNC Machining",
      description: "Precision CNC machining services for complex geometries and tight tolerances. Our advanced equipment handles everything from prototypes to high-volume production.",
      image: "/precision-engineering-stainless-steel.jpg"
    },
    {
      icon: Scissors,
      title: "Laser Cutting",
      description: "State-of-the-art laser cutting technology for precise, clean edges on stainless steel sheets and plates. Perfect for intricate designs and complex shapes.",
      image: "/manufacturing-facility-sparks-metal-cutting.jpg"
    },
    {
      icon: Wrench,
      title: "Welding & Fabrication",
      description: "Professional welding and fabrication services using TIG, MIG, and plasma welding techniques. Certified welders ensure strong, durable connections.",
      image: "/stainless-steel-welding-sparks.jpg"
    },
    {
      icon: Sparkles,
      title: "Surface Finishing",
      description: "Comprehensive finishing services including polishing, brushing, bead blasting, passivation, and electropolishing to meet your exact specifications.",
      image: "/professional-stainless-steel-finishing.jpg"
    },
    {
      icon: Ruler,
      title: "Precision Forming",
      description: "Bending, rolling, and forming services for custom shapes and configurations. Our equipment handles various thicknesses and complex geometries.",
      image: "/industrial-steel-sheet.jpg"
    },
    {
      icon: CheckCircle,
      title: "Quality Assurance",
      description: "Rigorous quality control with dimensional inspection, material testing, and full traceability. All parts meet or exceed industry standards.",
      image: "/industrial-surface-treatment-facility.jpg"
    }
  ]

  return (
    <section className="py-16 md:py-24 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h2 className="text-4xl md:text-5xl font-bold text-gray-900 mb-4">
            Our Manufacturing Capabilities
          </h2>
          <p className="text-lg text-gray-600 max-w-3xl mx-auto">
            We offer comprehensive manufacturing services to transform your designs into high-quality stainless steel parts. From initial concept to final delivery, we handle every step of the process.
          </p>
        </div>
        
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {capabilities.map((capability, index) => {
            const Icon = capability.icon
            return (
              <div key={index} className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-xl transition flex flex-col border border-gray-200">
                <div className="relative h-48">
                  <Image
                    src={capability.image}
                    alt={capability.title}
                    fill
                    className="object-cover"
                  />
                  <div className="absolute top-4 right-4 bg-blue-600 p-3 rounded-full">
                    <Icon className="w-6 h-6 text-white" />
                  </div>
                </div>
                <div className="p-6 flex flex-col flex-grow">
                  <h3 className="text-xl font-bold text-gray-900 mb-3">{capability.title}</h3>
                  <p className="text-gray-600 leading-relaxed flex-grow">{capability.description}</p>
                </div>
              </div>
            )
          })}
        </div>
      </div>
      <QuoteModal 
        open={isQuoteModalOpen} 
        onClose={() => setIsQuoteModalOpen(false)}
        source="Customized Parts Capabilities"
      />
    </section>
  )
}

